.Nav{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    line-height: 100px;
}

.Nav .title{
    font-size: 20px;
    font-weight: bold;
}

.Nav .list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.Nav .list li{
    margin: 0 5px;
    padding: 0 20px;
}

.Nav .list .current{
    color: #f86400;
}


